/* Profile */
.profile__text h3,
.profile__text h6 {
  text-align: left;
}

/* Travel Map */
.travel-map__container {
  height: 100%;
}

.travel-map {
  margin: auto;
  padding-top: 10px;
  
  height: 60%;
  width: 90%;
}

.info-window img {
  width: 100pt;
}

.info-window p {
  font-size: 14px;
  margin-bottom: 0.25rem;
}

/* Albums Gallery */
.card {
  width: 100%;
  box-shadow: 0 6px 10px rgba(0,0,0,.08), 0 0 6px rgba(0,0,0,.05);
}

.card:hover {
  box-shadow: 0 10px 20px rgba(0,0,0,.12), 0 4px 8px rgba(0,0,0,.06);
}
